// app/levels/[id]/page.tsx - 保持现有文件位置不变
import data from "@/data/levels.json";
import LevelDetail from "@/components/LevelDetail";
import Levels from "@/components/Levels";
import { Metadata } from "next";

// 定义 Level 类型 - 添加 category 字段
interface Level {
  id: number;
  category: string; // 确保有分类字段
  title: string;
  videoId: string;
  thumb: string;
}

// 动态生成元数据 - 保持原有参数结构
export async function generateMetadata({ params }: { params: { id: string } }): Promise<Metadata> {
  try {
    const levelId = Number(params.id);
    const levels = data as Level[];
    
    // 查找关卡（如果有多个相同ID，使用第一个）
    const level = levels.find(l => l.id === levelId);
    
    if (!level) {
      return {
        title: `Perfect Tidy Level ${levelId} Guide – Tips & Strategies`,
        description: `Complete walkthrough and solutions for Perfect Tidy level ${levelId}. Find tips and strategies to master this level and progress through the game.`,
        alternates: {
          canonical: `https://www.hexasortlevel.com/levels/${levelId}`,
        },
      };
    }
    
    return {
      title: level.title,
      description: `Complete walkthrough and solutions for ${level.title}. Find tips and strategies to master this level and progress through the game.`,
      alternates: {
        canonical: `https://www.hexasortlevel.com/levels/${levelId}`,
      },
      openGraph: {
        title: level.title,
        description: `Complete walkthrough and solutions for ${level.title}. Find tips and strategies to master this level and progress through the game.`,
        url: `https://www.hexasortlevel.com/levels/${levelId}`,
        siteName: 'Perfect Tidy Level Guide',
        images: [
          {
            url: level.thumb,
            width: 1200,
            height: 630,
            alt: level.title,
          },
        ],
        locale: 'en_US',
        type: 'website',
      },
      twitter: {
        card: 'summary_large_image',
        title: level.title,
        description: `Complete walkthrough and solutions for ${level.title}. Find tips and strategies to master this level and progress through the game.`,
        images: [level.thumb],
      },
      robots: {
        index: true,
        follow: true,
        nocache: false,
        googleBot: {
          index: true,
          follow: true,
          noimageindex: false,
          'max-video-preview': -1,
          'max-image-preview': 'large',
          'max-snippet': -1,
        },
      },
    };
  } catch (error) {
    console.error('Error generating metadata:', error);
    return {
      title: 'Perfect Tidy Level Guide – Tips & Strategies',
      description: 'Complete walkthrough and solutions for Perfect Tidy levels. Find tips and strategies to master the game.',
    };
  }
}

// 生成静态路径参数 - 保持原有逻辑
export async function generateStaticParams() {
  return (data as Level[]).map((l) => ({ id: String(l.id) }));
}

// 详情页 - 添加分类上下文支持
export default async function Page({ params }: { params: { id: string } }) {
  try {
    const levelId = Number(params.id);
    const levels = data as Level[];

    // 查找所有匹配该ID的关卡（可能有多个分类）
    const matchedLevels = levels.filter(l => l.id === levelId);
    
    if (matchedLevels.length === 0) {
      return (
        <div className="min-h-screen flex items-center justify-center">
          <div className="text-center p-8">
            <h1 className="text-2xl font-bold text-gray-800 mb-4">Level Not Found</h1>
            <p className="text-gray-600">The requested level does not exist.</p>
          </div>
        </div>
      );
    }

    // 使用第一个匹配的关卡（保持原有逻辑）
    const level = matchedLevels[0];
    
    // 获取当前关卡在全局列表中的位置
    const globalIdx = levels.findIndex(l => l.id === levelId);
    
    // 获取当前关卡在所属分类中的位置
    const categoryLevels = levels.filter(l => l.category === level.category);
    const categoryIdx = categoryLevels.findIndex(l => l.id === levelId);

    // 提供两种导航选项：
    // 1. 全局导航（原有逻辑）
    const globalPrev = globalIdx > 0 ? levels[globalIdx - 1] : null;
    const globalNext = globalIdx < levels.length - 1 ? levels[globalIdx + 1] : null;
    const globalAdjacent = levels.slice(Math.max(0, globalIdx - 12), globalIdx + 12);

    // 2. 分类内导航（新增）
    const categoryPrev = categoryIdx > 0 ? categoryLevels[categoryIdx - 1] : null;
    const categoryNext = categoryIdx < categoryLevels.length - 1 ? categoryLevels[categoryIdx + 1] : null;
    const categoryAdjacent = categoryLevels.slice(Math.max(0, categoryIdx - 12), categoryIdx + 12);

    return (
      <>
        {/* 添加分类信息显示 */}
        <div className="bg-blue-50 border-l-4 border-blue-400 p-4 mb-6">
          <div className="max-w-4xl mx-auto">
            <p className="text-blue-700">
              <span className="font-semibold">Category:</span> {level.category}
              {matchedLevels.length > 1 && (
                <span className="text-blue-600 text-sm ml-2">
                  (This level exists in {matchedLevels.length} categories)
                </span>
              )}
            </p>
          </div>
        </div>
        
        <LevelDetail 
          level={level} 
          // 传递两种导航选项，让组件决定使用哪种
          prev={categoryPrev || globalPrev} 
          next={categoryNext || globalNext} 
          adjacent={categoryAdjacent.length > 0 ? categoryAdjacent : globalAdjacent}
          // 传递分类信息
          category={level.category}
          availableCategories={matchedLevels.map(l => l.category)}
        />
        <Levels />
      </>
    );
  } catch (error) {
    console.error('Error rendering page:', error);
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center p-8">
          <h1 className="text-2xl font-bold text-gray-800 mb-4">Error Loading Level</h1>
          <p className="text-gray-600">An error occurred while loading the level. Please try again later.</p>
        </div>
      </div>
    );
  }
}